.game{
    display: flex;
    flex-direction: column;
    height:  100vh;
    justify-content: center;
    align-items: center;
}

.game-info{
  margin-top: 30px;
  margin-left:  0px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 25px;

}

.b{
  border-radius: 20px;
  cursor: pointer;
  background-color: hotpink;
  font-size:20px;
  align-items: center;
  justify-content: center;
  color: white;
  font-style: bold;
  width: 200px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  padding: 5px;
  box-shadow: 5px 5px   rgb(230, 209, 249);


}