.square{
    box-shadow: 2px 2px 2px 2px;
    background: #a4a5ef;
    border: 2px solid #63599d;
    font-size: 50px;  
    margin: -5px -1px 0 0;
    padding: 0;
    text-align: center;
    justify-content: center;
    align-items: center;   
    float: left;
    width: 80px;
    height: 80px;
    color: white;
    font-family: 'Black Han Sans', sans-serif;
    border-radius: 5px;
    cursor: pointer;

}

